.form-container {
    margin: 150px auto;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 70%;
    height: 400px;
    border: 1px solid rgb(13, 41, 165);
    outline: none;
    border-radius: 7px;
    background: black;
    color: white;
    padding: 10px;
    cursor: pointer;
}

.form-container input {
    width: 60%;
    height: 35px;
    outline: none;
    margin: 20px auto;
    border-radius: 5px;
    border: 2px solid rgb(72, 11, 184);
    cursor: pointer;
}

.form-container input::placeholder {
    text-align: center;
}

.form-container input:focus {
    outline: none;
    border: none;
}

@media screen and (max-width:700px) {
    .form-container {
        height: 550px;
        margin: 50px auto;
    }
    .form-container input {
        width: 90%;
    }
    .form-container h2 {
        overflow: scroll;
    }
}