.counter-container {
    padding: 10px;
}

.counter-container button {
    width: 120px;
    height: 35px;
    margin: 5px;
    padding: 5px;
    border-radius: 5px;
    background: black;
    color: white;
    font-size: 20px;
    border: 1px solid white;
    text-align: center;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.counter-container button:hover {
    border: 1 solid blue;
    box-shadow: -5px -5px 36px 10px rgba(33, 117, 217, 0.8) inset;
}